html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden; 
}

body {
    background: linear-gradient(to right, #feda75, #fa7e1e, #d62976, #962fbf, #4f5bd5);
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 20px;
    display: flex;
    flex-direction: column;  
    justify-content:  flex-start;;
    align-items: center;
    min-height: 100vh;

    /* height: 100vh; */
}

.app {
    width: 100%;
    max-width: 600px; /* Limit the width of the app for better focus */
    background: white; /* Clear and focused content area */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 0 15px rgba(0,0,0,0.2); /* Subtle shadow for depth */
    overflow: hidden; /* Ensures nothing spills out */
    overflow-y: auto;  
    margin: 20px; 
    box-sizing: border-box; 
}

.app-header {
    background-color: #5851DB; /* Instagram-like vibrant header */
    color: white;
    padding: 20px;
    text-align: center;
}

.main-content {
    padding: 20px;
    color: #333; /* Darker text for better readability */
}

.file-input-container {
    margin: 15px 0;
    padding: 10px;
    background-color: #E1E1E1; /* Light gray background for the file input area */
    border-radius: 5px;
}

.file-input-container label {
    display: block;
    margin-bottom: 5px;
    color: #333; /* Ensuring label is visible */
}



.container {
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.8);
    width: 90%;
    max-width: 600px;
}

h1 {
    margin-bottom: 20px;
}

input[type="file"] {
    width: 100%;
    padding: 8px 10px;
    border: 2px solid #CCCCCC; /* Subtle border for the input */
    border-radius: 4px;
    cursor: pointer;
}

input[type="file"]::-webkit-file-upload-button {
    background: #3897f0;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type="file"] {
    color: transparent;
}

