button {
    display: block;
    width: calc(100% - 40px);
    margin: 0 20px 20px;
    padding: 10px;
    background-color: #0095f6; /* Instagram-like button color */
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

button:hover {
    background-color: #276ab1;
    opacity: 0.9; /* Slight interactive effect on hover */
}

.list {
    list-style: none;
    padding: 0;
}

.list li {
    padding: 8px 0;
    border-bottom: 1px solid #EEE; /* Light separator for each item */
}
